import * as React from 'react'
import { Fragment } from 'react'
import { Listbox, Transition } from '@headlessui/react'
import { ChevronUpDownIcon } from '@heroicons/react/20/solid'

const ListSelector = ({ options, selected, onSelect }) => {
  return (
    <Listbox value={selected} onChange={onSelect}>
      {({ open }) => (
        <div className="relative">
          <Listbox.Button className="relative cursor-default rounded-md bg-white/10 hover:bg-white/20 py-1.5 px-8 focus:outline-none">
            <span className="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-2">
              <ChevronUpDownIcon className="h-5 w-5" aria-hidden="true" />
            </span>
            <span className="text-xs sm:text-sm xl:text-lg font-semibold tracking-tight  hero-text">{selected}</span>
          </Listbox.Button>
          <Transition
            show={open}
            as={Fragment}
            leave="transition ease-in-out duration-100"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <Listbox.Options className="absolute z-10 mt-1 max-h-56 bg-white/10 rounded-md overflow-hidden focus:outline-none">
              {options.map((option, idx) => (
                <Listbox.Option
                  key={idx}
                  className="hover:bg-white/20 relative cursor-default select-none py-1.5 px-8"
                  value={option}
                >
                  <div className="flex items-center">
                    <span>
                      {option}
                    </span>
                  </div>
                </Listbox.Option>
              ))}
            </Listbox.Options>
          </Transition>
        </div>
      )}
    </Listbox>
  )
}

export default ListSelector;
